.titleDecoration{
   width: 15%;    
}
.about{
    background-color: rgba(0, 0, 0, 0.04);
    margin-bottom: 0;
}
h4{
    font-weight: 600;
}
.skills {
    display: flex;
    flex-wrap: wrap;
    margin-top:0 ;
    padding-top: 0;
 }
.skill {
      margin-top: 1rem ;
      padding: 0.75rem 1.5rem;
