      border-radius: 8px;
      font-weight: 600; 
      color: #777;
      box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.15);
     }
 @media only screen and (max-width: 785px) {     
.aboutitems{

    margin-left: 4vw;
 } 
 .skill {
    margin-top: 0.5rem ;
    padding: 0.5rem 1rem;
    margin-right: 0.5rem;
    font-size: 1rem;
    background: rgba(255, 255, 255, 0.87);
    border-radius: 8px;
    font-weight: 600; 
    color: #777;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.15);
   }
}
